import * as React from "react"
import { useStaticQuery, type HeadFC, type PageProps, graphql, Link } from "gatsby"
import AppContent from "../components/AppContent"
import Dec from "../components/Dec"
import Head_ from "../components/Head_"

const IndexPage: React.FC<PageProps> = () => {
  const posts = useStaticQuery(graphql`
  query Query {
    allMarkdownRemark {
      nodes {
        frontmatter{
          title
          date
          id
          tags
        }
      }
    }
  }
`)

const post = posts.allMarkdownRemark.nodes.map((item:any) => 
  <div className="pt-2 relative overflow-hidden bg-gray-100 dark:border-gray-700 dark:bg-gray-800 border-gray-300 border rounded-xl md:pl-2 flex flex-col gap-1 items-center md:items-start pb-2 select-none" key={item.frontmatter.id}>
  <Link to={"/posts/"+item.frontmatter.id} className="text-lg z-[1] transition-all font-semibold dark:text-gray-50">{item.frontmatter.title}</Link>
  <div className="text-gray-700 dark:text-gray-400 flex flex-col gap-1 z-[1]">
  <div className="flex flex-row gap-2 md:text-start text-center">{item.frontmatter.tags.map((item_:any,i:Number)=><Link key={item.frontmatter.id+'-'+item_.text+'-'+i} className="hover:text-gray-900 dark:hover:text-gray-100 transition-all" to={"/tags/"+item_}>ğŸ·ï¸&nbsp;{item_}</Link>)}</div>
  <div className="text-center md:text-start">{item.frontmatter.date}</div>
  </div>
  <Link to={"/posts/"+item.frontmatter.id} className="h-10 z-[1] w-24 flex items-center justify-center self-end mr-2 font-semibold rounded-xl transition-all hover:bg-gray-300/40 dark:hover:bg-gray-900/40 dark:text-gray-50">é˜…è¯»æ›´å¤š&nbsp;â¡ï¸</Link>
  <div className="absolute text-6xl -z-[0] right-3 text-gray-400/40 rotate-45 font-bold">{item.frontmatter.title}</div>
  </div>)
  return (
    <AppContent setTheme={undefined}>
      <Dec />
      <div className="pt-4 gap-3 w-[98%] grid md:grid-cols-2 lg:grid-cols-3 grid-cols-1">
        {post}
      </div>
    </AppContent>
  )
}

export default IndexPage

export const Head: HeadFC = () =>  <><title>é¦–é¡µ</title><Head_ /><script dangerouslySetInnerHTML={{__html:`if (localStorage.ti == undefined && window.matchMedia('(prefers-color-scheme:dark)').matches || localStorage.ti == 1 && window.matchMedia('(prefers-color-scheme:dark)').matches) {document.documentElement.classList.add('dark')} else if (localStorage.ti==0){document.documentElement.classList.add('dark')}`}}></script></>
